/* Default styles for landscape orientation */
.iframeWrapper {
    position: relative;
    padding-bottom: 56.25%; /* 16:9 */
    padding-top: 25px;
    height: 0;
  }
  
  .iframeWrapper iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
  }
  
  /* Media query for portrait orientation */
  @media (orientation: portrait) {
    .iframeWrapper {
      padding-bottom: 100%; /* Adjust as needed for portrait orientation */
      padding-top: 0;
    }
  }